import React from "react";
import "./Sentence.css";

const ReviewDetail = ({ score, text }) => {
	if (!text) {
		return <div>Loading...</div>;
	}

	let sentiment;
	if (score > 0.5) {
		sentiment = "positive"
	}
	else if (score < -0.5) {
		sentiment = "negative"
	}

	return (
		<div className="d-inline {sentiment}">
			{text}
		</div>
	) 
};

export default ReviewDetail;



